<template>
  <div>
    <div
      @click="getTo"
      style="
        color: #c1c1c1;
        font-size: 10px;
        margin-top: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
      "
    >
      <van-image
        src="https://card-img1.oss-cn-beijing.aliyuncs.com/cyrh/icon/logoIcon.png"
        width="12px"
        height="12px"
        class="m-r-4"

      />
      由阿库技术提供技术支持
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    getTo() {
    //   wx.miniProgram.navigateTo({
    //     url: 'pages/car/index' // 小程序内页面路径
    // });
      wx.navigateToMiniProgram({
        appid:'wx49744bff560f0d70',
        path:'pages/index/index'
      })
      // const appScheme =
      //   "weixin://dl/business/?appid=wx49744bff560f0d70&path=pages/index/index";
      // console.log(appScheme);
      // window.location.href = appScheme;
    },
  },
};
</script>

<style>
    .m-r-4{
      margin-right: 4px
    }
</style>



