<template>
  <div class="contentBox m-x">
    <van-image
      v-if="isHeader"
      :src="IMG + headerImg"
      :width="width"
      :height="height"
      class="image"
    ></van-image>
    <slot name="content"> </slot>
  </div>
</template>

<script>
export default {
  props: {
    headerImg: {
      type: String,
      default: "",
    },
    width: {
      type: String,
      default: "328px",
    },
    height: {
      type: String,
      default: "31px",
    },
    isHeader: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      IMG: this.IMG,
    };
  },
};
</script>

<style lang="scss" scoped>
.m-x {
  margin: 12px 0 !important;

}
.image{
  margin: auto;
  display: flex;
  margin-bottom: 10px;
}
</style>



