<template>
  <div>
    <van-image :src="IMG+cardName+'/cardBg/cardBg'+cardId+'.png'"></van-image>
  </div>
</template>

<script>
export default {
      props:{
          cardName:String,
          cardId:Number
      },
      data(){
        return{
          IMG:this.IMG
        }
      }
}
</script>

<style>

</style>



