<template>
  <div class="itInfo">
    <van-image :src="IMG + phoneButton" @click="getPhone(phone)"></van-image>
    <van-image
      :src="IMG + addressButton"
      @click="getAddress(address,name)"
    ></van-image>
  </div>
</template>

<script>
export default {
  props: {
    phoneButton: String,
    addressButton: String,
    phone: String,
    address: String,
    name:{
      type:String,
      default:'正在加载'
    }
  },
  data() {
    return {
      IMG: this.IMG,
    };
  },
  methods: {
    // 电话
    getPhone(phone) {
      console.log("拨打电话");
      window.location.href = "tel:" + phone;
    },
    // 地址
    getAddress(address,name) {
      this.$router.push({
        path: "/map",
        query: { address: address,name:name },
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.itInfo {
  justify-content: space-between;
  display: flex;
  width: 100%;
  height: 66px;
  background: #ffffff;
  border-radius: 8px;
  opacity: 1;
  margin: 0 auto;
  margin: 6px 0;
  padding: 15px 12px;
  box-sizing: border-box;

  image {
    height: 100%;
  }
}
</style>



